.w-nav {
  position: relative;
  background: #dddddd;
  z-index: 1000;
}
.navbar {
  position: fixed;
  top:0;
  display: block;
  height:60px;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, .1);
}
.w-nav:before, .w-nav:after {
  content: " ";
  display: table;
  grid-column-start: 1;
  grid-row-start: 1;
  grid-column-end: 2;
  grid-row-end: 2;
}
.w-nav:after {
  clear: both;
}
.w-container {
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: 850px;
}
.w-container:before, .w-container:after {
  content: " ";
  display: table;
  grid-column-start: 1;
  grid-row-start: 1;
  grid-column-end: 2;
  grid-row-end: 2;
}

.w-container:after {
  clear: both;
}

.w-nav-brand {
  height: 70%;
  display: flex;
  align-items: center;
  position: relative;
  float: left;
  text-decoration: none;
  color: #333333;
}

.h-logo {
  max-height: 50px;
  max-width: 80%;
  margin-top: 10px;
  margin-bottom: 10px;
}

.kCEMFk {
  margin-left: 10px;
  
  padding: 10px;
}
.kCEMFk img {
  height: 30px;
  width: 30px;
  margin-top: 0;
}

.w-nav-menu {
  position: relative;
  float: right;
}

.w-nav-menu {
  height: 100%;
  display: flex;
  align-items: center;
}

.nav-link {
  -webkit-transition: color 200ms ease;
  transition: color 200ms ease;
  font-family: Inter, sans-serif;
  color: #5f6368;
  font-weight: 600;
}

.w-nav-link {
  position: relative;
  display: inline-block;
  vertical-align: top;
  text-decoration: none;
  color: #222222;
  padding: 20px;
  text-align: left;
  margin-left: auto;
  margin-right: auto;
}

.w-nav-overlay {
  position: absolute;
  overflow: hidden;
  display: none;
  top: 100%;
  left: 0;
  right: 0;
  width: 100%;
}

* {
  box-sizing: border-box;
}
.image-2 {
